<template>
  <div id="app" style="background-color: rgb(233, 231, 231); height:100%;">
    <HeaderNavVar />
    <div>
        <router-view></router-view>
    </div>
  </div>
</template>

<script>
import HeaderNavVar from "./components/layout/Header.vue";

export default {
  name: "App",
  data() {
    return {
      isMobile: false,
      pcStandard: 769, // PC 화면의 Viewport 기준점
      mobileOption: {
        // 모바일 옵션 지정
        slidesPerView: 1,
        effect: "fade",
        fadeEffect: {
          crossFade: true,
        },
        loop: true,
      },
      pcOption: {
        // PC 옵션 지정
        slidesPerView: 3,
      },
    };
  },
  methods: {
    checkIsMobile: function () {
      this.isMobile = (window.innerWidth < this.pcStandard)
    },
    handleResize: function () {
      this.$nextTick(function () {
        this.checkIsMobile()
      })
    }
  },
  components: {
    HeaderNavVar
  },
};
</script>

<style>

@import url('https://fonts.googleapis.com/css2?family=Nanum+Brush+Script&family=Song+Myung&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Gowun+Batang:wght@700&family=Nanum+Brush+Script&family=Song+Myung&display=swap');

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

</style>
