<template>
  <div id="title">
    <!-- Masthead-->
    <header class="masthead" style="height: 1024px; position: relative">
      <canvas
        id="canvas"
        style="
          width: 100%;
          position: absolute;
          top: 0px;
          left: 0px;
          height: 1024px;
        "
      ></canvas>
      <div class="container">
        <div class="masthead-dday" id="title_dday">
          <router-link to="/storypage" class="router-link-class"> D-DAY : {{ dDay }} </router-link>
        </div>
        <div class="masthead-comment" id="title_one" style="transition: all 3s">
          You're Invited
        </div>
        <div class="masthead-heading" id="title_two">현준♥혜인 결혼합니다.</div>
        <div class="masthead-subheading" id="title_three">
          2024.03.30 오후 3시
        </div>
        <div class="masthead-subheading" id="title_four">
          합정 웨딩 시그니처
        </div>
        <!-- <a class="btn btn-primary btn-sm text-uppercase" href="#services">Tell Me More</a> -->
      </div>
    </header>
  </div>
</template>
<script>
import "../../router.js";
import { getDatabase } from "firebase/database";
export default {
  name: "HeaderNavVar",
  data() {
    return {
      dDay: "",
    };
  },
  methods: {
    diffDay() {
      const dDayTime = new Date("2024-03-30T15:00:00+0900");
      const todayTime = new Date();

      const diff = dDayTime - todayTime;

      const diffDay = Math.floor(diff / (1000 * 60 * 60 * 24));
      const diffHour = Math.floor((diff / (1000 * 60 * 60)) % 24);
      const diffMin = Math.floor((diff / (1000 * 60)) % 60);
      const diffSec = Math.floor((diff / 1000) % 60);
      this.dDay =
        diffDay +
        "일 " +
        diffHour +
        "시간 " +
        diffMin +
        "분 " +
        diffSec +
        "초 ";
    },
  },
  mounted() {
    this.diffDay();
    setInterval(this.diffDay, 1000);

    const canvas = document.querySelector("#canvas");
    canvas.width = window.innerWidth;
    canvas.height = window.innerHeight;
    const ctx = canvas.getContext("2d");

    const TOTAL = 150;
    const petalArray = [];

    const petalImg = new Image();
    (petalImg.src = require("../../assets/img/petal.png")),
      (petalImg.onload = () => {
        for (let i = 0; i < TOTAL; i++) {
          petalArray.push(new Petal());
        }
        render();
      });

    function render() {
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      petalArray.forEach((petal) => {
        petal.animate();
      });

      window.requestAnimationFrame(render);
    }

    // 벚꽃 잎 클래스
    class Petal {
      constructor() {
        this.x = Math.random() * canvas.width;
        this.y = Math.random() * canvas.height * 2 - canvas.height;
        this.w = 15 + Math.random() * 15;
        this.h = 8 + Math.random() * 10;
        this.opacity = this.w / 80;
        this.xSpeed = 2 + Math.random();
        this.ySpeed = 2 + Math.random() * 0.5;
        this.flip = Math.random();
        this.flipSpeed = Math.random() * 0.03;
      }

      draw() {
        if (this.y > canvas.height || this.x > canvas.width) {
          this.x = -petalImg.width;
          this.y = Math.random() * canvas.height * 2 - canvas.height;
          this.xSpeed = 2 + Math.random();
          this.ySpeed = 1 + Math.random() * 0.5;
          this.flip = Math.random();
        }
        ctx.globalAlpha = this.opacity;
        ctx.drawImage(
          petalImg,
          this.x,
          this.y,
          this.w * (0.66 + Math.abs(Math.cos(this.flip)) / 3),
          this.h * (0.8 + Math.abs(Math.sin(this.flip)) / 2)
        );
      }

      animate() {
        this.x += this.xSpeed;
        this.y += this.ySpeed;
        this.draw();
        this.flip += this.flipSpeed;
      }
    }
  },
};
</script>
<style>
#title_one,
#title_two,
#title_three,
#title_four,
#title_dday {
  animation: fadeInAnimation ease 1.5s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}
@keyframes fadeInAnimation {
  0% {
    opacity: 0;
    transform: translate3d(0, 5%, 0);
  }
  100% {
    opacity: 1;
    transform: translateZ(0);
  }
}

#title_dday {
  opacity: 0;
  animation-delay: 2.5s;
}

#title_one {
  opacity: 0;
  animation-delay: 0.5s;
}

#title_two {
  opacity: 0;
  animation-delay: 1s;
}

#title_three {
  opacity: 0;
  animation-delay: 1.5s;
}

#title_four {
  opacity: 0;
  animation-delay: 2s;
}

.router-link-class {
  color: inherit;
  text-decoration: none;
}

.router-link-class:visited {
  color: inherit;
  text-decoration: none;
}

.router-link-class:focus {
  color: inherit;
  text-decoration: none;
}

.router-link-class:hover {
  color: inherit;
  text-decoration: none;
}

.router-link-class:active {
  color: inherit;
  text-decoration: none;
}
</style>