<template>
  <div
    id="guestbook"
    data-aos="fade-up"
    data-aos-offset="120"
    data-aos-delay="50"
    data-aos-duration="1200"
    data-aos-easing="ease-in-out"
    data-aos-mirror="true"
    data-aos-once="true"
    data-aos-anchor-placement="top-bottom"
    style="
      background-color: rgb(233, 231, 231);
      width: 100%;
      margin-top: 20px;
      padding: 5px;
    "
  >
    <h1>방명록</h1>
    <p>"남겨주신 글은 오래도록 마음에 간직하겠습니다"</p>
    <div style="margin-bottom:10px;">
      <button @click="showGuestBookModal = true">방명록 작성하기</button>
    </div>
    <div>
      <VueSlickCarousel v-bind="settings" v-if="messages.length">
        <div v-for="message in messages" v-bind:key="message.id" @click="pushMessages(message)">
            <h3>{{ message.title }}</h3>
            <h5>{{ message.username }}</h5>
            <div class="messageBox">{{ message.newmessage }}</div>
        </div>
      </VueSlickCarousel>
    </div>
    <GuestBookModal
      v-if="showGuestBookModal"
      @close="showGuestBookModal = false"
    >
      <h3 slot="header">
        <i
          class="fas fa-times closeModalBtn"
          @click="showGuestBookModal = false"
        ></i>
      </h3>

      <div slot="body">
        <p style="text-align: left">
          <br />
          새로이 시작하는 신랑 신부에게
          <br />
          따뜻한 격려와 축하의 한마디 부탁드립니다!
        </p>
        <div style="display: inline-grid; width: 90%">
          <div>
            <span class="inputSpanClass">제목</span>
            <input
              class="inputModalClass"
              ref="title"
              placeholder="20자이내"
              type="text"
              maxlength="20"
            />
          </div>
          <div>
            <span class="inputSpanClass">이름</span>
            <input
              class="inputModalClass"
              ref="username"
              placeholder="20자이내"
              type="text"
              maxlength="20"
            />
          </div>
            <div>
            <span class="inputSpanClass">이메일</span>
            <input
              class="inputModalClass"
              ref="useremail"
              placeholder="20자이내"
              type="text"
              maxlength="20"
            />
          </div>
          <div>
            <span class="inputSpanClass">인사말</span>
            <textarea
              class="inputModalClass"
              ref="newmessage"
              placeholder="인사말을 입력해주세요(200자 이내)"
              style="height: 200px"
              maxlength="200"
              wrap="hard"
            ></textarea>
          </div>
        </div>
        <div>
          <button @click="addNewMessage">등록하기</button>
        </div>
      </div>
    </GuestBookModal>
     <GuestBookViewModal
      v-if="showGuestBookViewModal"
      @close="showGuestBookViewModal = false"
    >
      <h3 slot="header">
        <i
          class="fas fa-times closeModalBtn"
          @click="showGuestBookViewModal = false"
        ></i>
      </h3>

      <div slot="body">
        <p style="text-align: left">
        </p>
        <div style="display: inline-grid; width: 90%">
          <div>
            <span class="inputSpanClass">제목</span>
             {{globalMessages.title}}
          </div>
          <div>
            <span class="inputSpanClass">이름</span>
            {{globalMessages.username}}
          </div>
          <div>
            <span class="inputSpanClass">인사말</span>
            {{globalMessages.newmessage}}
          </div>
        </div>

      </div>
    </GuestBookViewModal>
  </div>
</template>

<script>
import {
  getFirestore,
  onSnapshot,
  collection,
  doc,
  setDoc,
  addDoc,
  orderBy,
  query,
} from "firebase/firestore";
import firebase from "firebase/compat/app";
import { onUnmounted, ref } from "vue";
import GuestBookModal from "../modal/GuestBookModal.vue";
import GuestBookViewModal from "../modal/GuestBookViewModal.vue";
import VueSlickCarousel from "vue-slick-carousel";
import "vue-slick-carousel/dist/vue-slick-carousel.css";
import "vue-slick-carousel/dist/vue-slick-carousel-theme.css";

const firebaseConfig = {
  apiKey: "AIzaSyA6I33lkBA-LfgMX8jIcjeXsZNGi8PLa54",
  authDomain: "weddinginvitation-8bc44.firebaseapp.com",
  projectId: "weddinginvitation-8bc44",
  storageBucket: "weddinginvitation-8bc44.appspot.com",
  messagingSenderId: "951402829854",
  appId: "1:951402829854:web:bff7a2c2e0f3b057e6760f",
  measurementId: "G-2858K50PBQ",
};

const app = firebase.initializeApp(firebaseConfig);
const db = getFirestore(app);

export default {
  name: "GuestBook",
  data() {
    return {
      messages: ref([]),
      showGuestBookModal: false,
      showGuestBookViewModal: false,
      settings: {
        dots: false,
        focusOnSelect: false,
        infinite: false,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 3,
        touchThreshold: 5,
      },
      globalMessages:[]
    };
  },
  components: {
    GuestBookModal,
    GuestBookViewModal,
    VueSlickCarousel,
  },
  methods: {
    getMessage: function () {
      const latestQuery = query(collection(db, "messages"), orderBy("date"));
      const livemessages = onSnapshot(latestQuery, (snapshot) => {
        this.messages = snapshot.docs.map((doc) => {
          return {
            id: doc.id,
            title: doc.data().title,
            username: doc.data().username,
            useremail: doc.data().useremail,
            newmessage: doc.data().newmessage,
            date: doc.data().date,
          };
        });
      });
      onUnmounted(livemessages);
    },
    addNewMessage: function () {
      if (confirm("등록하시겠습니까?")) {
        addDoc(collection(db, "messages"), {
          title: this.$refs.title.value,
          username: this.$refs.username.value,
          useremail: this.$refs.useremail.value,
          newmessage: this.$refs.newmessage.value,
          date: Date.now(),
        });
        this.showGuestBookModal = false;
      }
    },
    updateMessage: function (message) {
      setDoc(doc(db, "messages", message.id), {
        title: message.title,
        username: message.username,
        newmessage: message.newmessage,
        date: Date.now(),
      });
    },
    pushMessages: function (text) {
      this.showGuestBookViewModal = true,
      console.log(text);
      this.globalMessages = text
    }
  },
  mounted() {
    const latestQuery = query(collection(db, "messages"), orderBy("date"));
    const livemessages = onSnapshot(latestQuery, (snapshot) => {
      this.messages = snapshot.docs.map((doc) => {
        return {
          id: doc.id,
          title: doc.data().title,
          username: doc.data().username,
          useremail: doc.data().useremail,
          newmessage: doc.data().newmessage,
          date: doc.data().date,
        };
      });
    });
    console.log(livemessages);
    onUnmounted(livemessages);
  },

};
</script>

<style>
.inputSpanClass {
  text-align: left;
  padding: 0;
  margin-bottom: 5px;
}
.inputModalClass {
  width: 80%;
  float: right;
  padding: 0;
  margin-bottom: 5px;
}
input::placeholder {
  font-size: 12px;
}
textarea::placeholder {
  font-size: 12px;
}

.messageBox {
  height: 100px;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>
